import React, { useState, useEffect } from "react";
import { Audio } from "expo-av";
import {
  View,
  StyleSheet,
  Dimensions,
  TouchableOpacity,
  Text,
  Image,
  ImageBackground,
} from "react-native";
import { GameEngine, GameLoop } from "react-native-game-engine";
import Modal from "react-native-modal";

const { width, height } = Dimensions.get("window");

const PlayerSize = 50;
const ObstacleSize = 50;
const ObstacleHeight = height / 2 - 14 - 10 - ObstacleSize;

const Gravity = 1;
const JumpForce = -120;
const InitialObstacleSpeed = 5;
const Acceleration = 1;

const Game = () => {
  const [playerPosition, setPlayerPosition] = useState({
    x: width / 2 - PlayerSize / 2,
    y: height / 2 - 14 - 10 - PlayerSize,
  });
  const [obstaclePosition, setObstaclePosition] = useState(width);
  const [gameOver, setGameOver] = useState(false);
  const [modalVisible, setModalVisible] = useState(false);
  const [score, setScore] = useState(0);
  const [obstacleSpeed, setObstacleSpeed] = useState(InitialObstacleSpeed);
  const [sound, setSound] = useState(null);

  useEffect(() => {
    const loadSound = async () => {
      const { sound } = await Audio.Sound.createAsync(
        require("./assets/island.mp3"),
        { shouldPlay: true, isLooping: true }
      );
      setSound(sound);
      await sound.playAsync();
    };

    loadSound();

    // Cleanup the sound when the component unmounts
    return () => {
      if (sound) {
        sound.unloadAsync();
      }
    };
  }, []);

  useEffect(() => {
    if (gameOver) {
      return;
    }

    const interval = setInterval(() => {
      setObstaclePosition((prevPosition) => prevPosition - obstacleSpeed);
    }, 16);

    return () => clearInterval(interval);
  }, [gameOver, obstacleSpeed]);

  useEffect(() => {
    if (obstaclePosition + ObstacleSize < 0) {
      setObstaclePosition(width);
      setScore((prevScore) => prevScore + 1);
      setObstacleSpeed((prevSpeed) => prevSpeed + Acceleration);
    }
  }, [obstaclePosition]);

  const onEvent = (e) => {
    if (e.type === "game-over" && !gameOver) {
      setGameOver(true);
      setModalVisible(true);
    } else if (e.type === "jump" && !gameOver) {
      if (playerPosition.y === height / 2 - 14 - 10 - PlayerSize) {
        setPlayerPosition((prevPosition) => ({
          ...prevPosition,
          y: prevPosition.y + JumpForce,
        }));
      }
    }
  };

  const resetGame = () => {
    setPlayerPosition({
      x: width / 2 - PlayerSize / 2,
      y: height / 2 - PlayerSize,
    });
    setObstaclePosition(width);
    setGameOver(false);
    setModalVisible(false);
    setScore(0);
    setObstacleSpeed(InitialObstacleSpeed);
  };

  const updateGame = () => {
    if (gameOver) {
      return;
    }

    setPlayerPosition((prevPosition) => {
      const newPosY = prevPosition.y + Gravity;
      if (newPosY >= height / 2 - 14 - 10 - PlayerSize) {
        return { ...prevPosition, y: height / 2 - 14 - 10 - PlayerSize };
      }
      return { ...prevPosition, y: newPosY };
    });

    const obstacleLeft = obstaclePosition;
    const obstacleRight = obstacleLeft + ObstacleSize;
    const obstacleTop = height / 2 - ObstacleSize;
    const obstacleBottom = obstacleTop + ObstacleSize;
    const playerLeft = playerPosition.x;
    const playerRight = playerLeft + PlayerSize;
    const playerTop = playerPosition.y;
    const playerBottom = playerTop + PlayerSize;

    if (
      playerRight >= obstacleLeft &&
      playerLeft <= obstacleRight &&
      playerBottom >= obstacleTop &&
      playerTop <= obstacleBottom
    ) {
      onEvent({ type: "game-over" });
    }
  };

  const renderPlayer = () => {
    return (
      <Image
        source={require("./assets/player.jpg")}
        style={[
          styles.player,
          { left: playerPosition.x, top: playerPosition.y },
          { backgroundColor: "transparent" },
        ]}
        resizeMode="contain"
      />
    );
  };

  const renderObstacle = () => {
    return (
      <Image
        source={require("./assets/candle.png")}
        style={[
          styles.obstacle,
          { left: obstaclePosition, top: ObstacleHeight },
          { backgroundColor: "transparent" },
        ]}
      />
    );
  };

  return (
    <View style={styles.container}>
      <GameEngine style={styles.gameContainer} systems={[]} entities={{}}>
        <GameLoop onUpdate={updateGame}>
          <TouchableOpacity
            style={styles.touchArea}
            onPress={() => onEvent({ type: "jump" })}
          >
            {renderPlayer()}
          </TouchableOpacity>
          {renderObstacle()}
          <View style={styles.floor} />
          <View style={styles.scoreContainer}>
            <Text style={styles.scoreText}>Score: {score}</Text>
          </View>
        </GameLoop>
      </GameEngine>

      <Modal isVisible={modalVisible} backdropOpacity={0.5}>
        <View style={styles.cardBackground}>
          <ImageBackground
            source={require("./assets/card.jpg")}
            style={styles.cardContent}
            resizeMode="contain"
          >
            <Text style={styles.modalText}>祝蝦米 {score} 歲 生日快樂</Text>
            <TouchableOpacity style={styles.modalButton} onPress={resetGame}>
              <Text style={styles.modalButtonText}>再過一次生日</Text>
            </TouchableOpacity>
          </ImageBackground>
        </View>
      </Modal>
      <View style={styles.footer}>
        <Text style={styles.footerText}>@copyright noirstar2023</Text>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    overflow: "hidden",
  },
  gameContainer: {
    flex: 1,
  },
  touchArea: {
    flex: 1,
  },
  player: {
    position: "absolute",
    width: PlayerSize,
    height: PlayerSize,
    backgroundColor: "transparent",
  },
  obstacle: {
    position: "absolute",
    width: ObstacleSize,
    height: ObstacleSize,
    backgroundColor: "transparent",
  },
  floor: {
    position: "absolute",
    bottom: height / 2 - 5,
    width: width,
    height: 10,
    backgroundColor: "gray",
  },
  scoreContainer: {
    position: "absolute",
    top: 20,
    left: 20,
  },
  scoreText: {
    fontSize: 20,
    fontWeight: "bold",
  },
  modalButton: {
    padding: 10,
    backgroundColor: "blue",
    borderRadius: 5,
  },
  modalButtonText: {
    color: "#fff",
    fontSize: 16,
    fontWeight: "bold",
  },
  footerText: {
    fontSize: 14,
    color: "gray",
  },
  cardBackground: {
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
  },
  cardContent: {
    borderRadius: 10,
    padding: 20,
    width: "100%",
    height: "100%",
    alignItems: "center",
    justifyContent: "center",
  },
  modalText: {
    fontSize: 20,
    fontWeight: "bold",
    marginBottom: 20,
    marginTop: 120,
    textAlign: "center",
  },
});

export default Game;
